import React, { useState, useEffect, useCallback } from 'react';
import { Language, LanguageCreateData, LanguageUpdateData } from '../types/Language';
import apiService from '../services/api';
import PageHeader from './PageHeader';

const Languages: React.FC = () => {
  const [languages, setLanguages] = useState<Language[]>([]);
  const [filteredLanguages, setFilteredLanguages] = useState<Language[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [editingLanguage, setEditingLanguage] = useState<Language | null>(null);
  const [alert, setAlert] = useState<{type: 'success' | 'error' | 'warning', message: string} | null>(null);
  const [loading, setLoading] = useState(false);

  const showAlert = (type: 'success' | 'error' | 'warning', message: string) => {
    setAlert({ type, message });
    setTimeout(() => {
      setAlert(null);
    }, 4000);
  };

  const filterLanguages = useCallback(() => {
    let filtered = languages.filter(language => {
      const matchesSearch = language.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           language.code.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           (language.nativeName && language.nativeName.toLowerCase().includes(searchTerm.toLowerCase()));
      const matchesStatus = statusFilter === 'all' ||
                           (statusFilter === 'active' && language.isActive) ||
                           (statusFilter === 'inactive' && !language.isActive);
      return matchesSearch && matchesStatus;
    });
    setFilteredLanguages(filtered);
  }, [languages, searchTerm, statusFilter]);

  const loadLanguages = useCallback(async () => {
    setLoading(true);
    try {
      const response = await apiService.getLanguages();
      const languagesData = Array.isArray(response) ? response : (response.results || response.languages || []);
      setLanguages(languagesData);
      setFilteredLanguages(languagesData);
    } catch (error: any) {
      showAlert('error', error.message || 'Erreur lors du chargement des langues');
      setLanguages([]);
      setFilteredLanguages([]);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    loadLanguages();
  }, [loadLanguages]);

  useEffect(() => {
    filterLanguages();
  }, [filterLanguages]);

  const handleCreateLanguage = async (languageData: LanguageCreateData) => {
    try {
      setLoading(true);
      const newLanguage = await apiService.createLanguage(languageData);
      await loadLanguages();
      setShowCreateModal(false);
      showAlert('success', `Langue ${newLanguage.name} cre avec succs`);
    } catch (error: any) {
      const errorMessage = error.data?.code?.[0] || error.data?.name?.[0] || error.message || 'Erreur lors de la cration de la langue';
      showAlert('error', errorMessage);
    } finally {
      setLoading(false);
    }
  };

  const handleEditLanguage = async (id: number, updates: LanguageUpdateData) => {
    try {
      setLoading(true);
      const updatedLanguage = await apiService.updateLanguage(id, updates);
      await loadLanguages();
      setEditingLanguage(null);
      showAlert('success', `Langue ${updatedLanguage.name} modifie avec succs`);
    } catch (error: any) {
      const errorMessage = error.data?.code?.[0] || error.data?.name?.[0] || error.data?.isActive?.[0] || error.message || 'Erreur lors de la modification de la langue';
      showAlert('error', errorMessage);
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteLanguage = async (id: number) => {
    const language = languages.find(l => l.id === id);
    if (language && window.confirm(`tes-vous sr de vouloir supprimer la langue ${language.name} ?`)) {
      try {
        setLoading(true);
        await apiService.deleteLanguage(id);
        await loadLanguages();
        showAlert('warning', `Langue ${language.name} supprime`);
      } catch (error: any) {
        const errorMessage = error.data?.error || error.message || 'Erreur lors de la suppression de la langue';
        showAlert('error', errorMessage);
      } finally {
        setLoading(false);
      }
    }
  };

  return (
    <>
      {alert && (
        <div className={`alert alert-${alert.type}`}>
          <div className="alert-content">
            <i className={`fas ${alert.type === 'success' ? 'fa-check-circle' : alert.type === 'error' ? 'fa-exclamation-circle' : 'fa-exclamation-triangle'}`}></i>
            <span>{alert.message}</span>
          </div>
          <button className="alert-close" onClick={() => setAlert(null)}>
            <i className="fas fa-times"></i>
          </button>
        </div>
      )}

      <PageHeader title="Gestion des Langues">
        <button className="btn-primary" onClick={() => setShowCreateModal(true)}>
          <i className="fas fa-plus"></i>
          <span>Ajouter Langue</span>
        </button>
      </PageHeader>

      <div className="users-content">
        {!loading && (
          <div style={{
            marginBottom: '24px',
            display: 'flex',
            gap: '16px',
            alignItems: 'center',
            flexWrap: 'wrap'
          }}>
            <div style={{
              flex: '1 1 300px',
              position: 'relative'
            }}>
              <i className="fas fa-search" style={{
                position: 'absolute',
                left: '16px',
                top: '50%',
                transform: 'translateY(-50%)',
                color: '#94A3B8',
                fontSize: '16px'
              }}></i>
              <input
                type="text"
                placeholder="Rechercher par nom, code ou nom natif..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                style={{
                  width: '100%',
                  padding: '12px 16px 12px 44px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box',
                  backgroundColor: '#ffffff'
                }}
                onFocus={(e) => {
                  e.target.style.borderColor = '#DC2626';
                  e.target.style.boxShadow = '0 0 0 3px rgba(220, 38, 38, 0.1)';
                }}
                onBlur={(e) => {
                  e.target.style.borderColor = '#E2E8F0';
                  e.target.style.boxShadow = 'none';
                }}
              />
            </div>

            <div style={{
              minWidth: '220px'
            }}>
              <select
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  fontWeight: '500',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box',
                  backgroundColor: '#ffffff',
                  cursor: 'pointer',
                  color: '#475569'
                }}
                onFocus={(e) => {
                  e.target.style.borderColor = '#DC2626';
                  e.target.style.boxShadow = '0 0 0 3px rgba(220, 38, 38, 0.1)';
                }}
                onBlur={(e) => {
                  e.target.style.borderColor = '#E2E8F0';
                  e.target.style.boxShadow = 'none';
                }}
              >
                <option value="all">Tous les statuts</option>
                <option value="active">Actives uniquement</option>
                <option value="inactive">Inactives uniquement</option>
              </select>
            </div>
          </div>
        )}

        {loading ? (
          <div style={{ textAlign: 'center', padding: 'var(--space-6)', color: 'var(--text-secondary)' }}>
            <i className="fas fa-spinner fa-spin" style={{ fontSize: '2rem' }}></i>
            <p style={{ marginTop: 'var(--space-4)' }}>Chargement...</p>
          </div>
        ) : (
          <>
            <div className="table-container">
              <table className="data-table">
                <thead>
                  <tr>
                    <th style={{ width: '100px', textAlign: 'left', paddingLeft: '16px' }}>Code</th>
                    <th style={{ width: '220px', textAlign: 'left' }}>Nom de la langue</th>
                    <th style={{ width: '200px', textAlign: 'left' }}>Nom natif</th>
                    <th style={{ width: '120px', textAlign: 'center' }}>Statut</th>
                    <th style={{ width: '100px', textAlign: 'center' }}>Ordre</th>
                    <th style={{ width: '140px', textAlign: 'center' }}>Utilisation</th>
                    <th style={{ width: '140px', textAlign: 'center' }}>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {filteredLanguages.length === 0 ? (
                    <tr>
                      <td colSpan={7} style={{ textAlign: 'center', padding: '3rem' }}>
                        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '1rem' }}>
                          <i className="fas fa-language" style={{ fontSize: '3.5rem', color: '#CBD5E0', opacity: 0.5 }}></i>
                          <div>
                            <p style={{ color: 'var(--text-secondary)', fontSize: '1.1rem', fontWeight: 500, margin: 0 }}>
                              Aucune langue trouve
                            </p>
                            {searchTerm && (
                              <p style={{ color: 'var(--text-tertiary)', fontSize: '0.9rem', marginTop: '0.5rem' }}>
                                Aucun rsultat pour "{searchTerm}"
                              </p>
                            )}
                          </div>
                        </div>
                      </td>
                    </tr>
                  ) : (
                    filteredLanguages.map((language) => (
                      <tr key={language.id}>
                        <td style={{ paddingLeft: '16px' }}>
                          <span style={{
                            display: 'inline-block',
                            padding: '6px 12px',
                            backgroundColor: '#FEE2E2',
                            color: '#991B1B',
                            borderRadius: '6px',
                            fontWeight: 700,
                            fontSize: '0.8rem',
                            textTransform: 'uppercase',
                            letterSpacing: '0.5px',
                            border: '1px solid #FCA5A5'
                          }}>
                            {language.code}
                          </span>
                        </td>
                        <td>
                          <span style={{ fontWeight: 600, fontSize: '0.95rem', color: '#2D3748' }}>
                            {language.name}
                          </span>
                        </td>
                        <td>
                          <span style={{ color: '#718096', fontSize: '0.9rem', fontStyle: language.nativeName ? 'normal' : 'italic' }}>
                            {language.nativeName || 'Non renseign'}
                          </span>
                        </td>
                        <td style={{ textAlign: 'center' }}>
                          <span style={{
                            display: 'inline-block',
                            padding: '5px 14px',
                            backgroundColor: language.isActive ? '#C6F6D5' : '#FED7D7',
                            color: language.isActive ? '#22543D' : '#742A2A',
                            borderRadius: '12px',
                            fontSize: '0.75rem',
                            fontWeight: 600,
                            textTransform: 'uppercase',
                            letterSpacing: '0.3px'
                          }}>
                            {language.isActive ? 'Active' : 'Inactive'}
                          </span>
                        </td>
                        <td style={{ textAlign: 'center' }}>
                          <div style={{ display: 'flex', justifyContent: 'center' }}>
                            <span style={{
                              display: 'inline-flex',
                              alignItems: 'center',
                              justifyContent: 'center',
                              width: '36px',
                              height: '36px',
                              backgroundColor: '#EDF2F7',
                              borderRadius: '8px',
                              fontSize: '0.9rem',
                              fontWeight: 600,
                              color: '#4A5568'
                            }}>
                              {language.displayOrder}
                            </span>
                          </div>
                        </td>
                        <td style={{ textAlign: 'center' }}>
                          <div style={{ display: 'flex', justifyContent: 'center' }}>
                            <span style={{
                              display: 'inline-flex',
                              alignItems: 'center',
                              gap: '6px',
                              padding: '7px 12px',
                              backgroundColor: language.participantCount && language.participantCount > 0 ? '#C6F6D5' : '#F7FAFC',
                              color: language.participantCount && language.participantCount > 0 ? '#22543D' : '#A0AEC0',
                              borderRadius: '8px',
                              fontSize: '0.85rem',
                              fontWeight: 500
                            }}>
                              <i className="fas fa-users" style={{ fontSize: '0.75rem' }}></i>
                              <span>{language.participantCount || 0}</span>
                            </span>
                          </div>
                        </td>
                        <td>
                          <div style={{ display: 'flex', gap: '8px', justifyContent: 'center' }}>
                            <button
                              style={{
                                padding: '8px 12px',
                                backgroundColor: '#FEE2E2',
                                border: '1px solid #FCA5A5',
                                borderRadius: '6px',
                                color: '#991B1B',
                                cursor: 'pointer',
                                transition: 'all 0.2s',
                                display: 'inline-flex',
                                alignItems: 'center',
                                gap: '4px'
                              }}
                              title="Modifier"
                              onClick={() => setEditingLanguage(language)}
                              onMouseOver={(e) => {
                                e.currentTarget.style.backgroundColor = '#FCA5A5';
                                e.currentTarget.style.transform = 'translateY(-1px)';
                              }}
                              onMouseOut={(e) => {
                                e.currentTarget.style.backgroundColor = '#FEE2E2';
                                e.currentTarget.style.transform = 'translateY(0)';
                              }}
                            >
                              <i className="fas fa-edit"></i>
                            </button>
                            <button
                              style={{
                                padding: '8px 12px',
                                backgroundColor: '#FFF5F5',
                                border: '1px solid #FED7D7',
                                borderRadius: '6px',
                                color: '#C53030',
                                cursor: 'pointer',
                                transition: 'all 0.2s',
                                display: 'inline-flex',
                                alignItems: 'center',
                                gap: '4px'
                              }}
                              title="Supprimer"
                              onClick={() => handleDeleteLanguage(language.id)}
                              onMouseOver={(e) => {
                                e.currentTarget.style.backgroundColor = '#FED7D7';
                                e.currentTarget.style.transform = 'translateY(-1px)';
                              }}
                              onMouseOut={(e) => {
                                e.currentTarget.style.backgroundColor = '#FFF5F5';
                                e.currentTarget.style.transform = 'translateY(0)';
                              }}
                            >
                              <i className="fas fa-trash"></i>
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>

            {filteredLanguages.length > 0 && (
              <div style={{
                marginTop: '1.5rem',
                padding: '1rem 1.5rem',
                backgroundColor: '#F7FAFC',
                borderRadius: '8px',
                borderLeft: '4px solid #4299E1',
                display: 'flex',
                alignItems: 'center',
                gap: '0.75rem'
              }}>
                <i className="fas fa-info-circle" style={{ color: '#4299E1', fontSize: '1.1rem' }}></i>
                <span style={{ color: '#2D3748', fontSize: '0.95rem' }}>
                  <strong style={{ fontWeight: 600 }}>{filteredLanguages.length}</strong> langue(s) trouve(s)
                  {searchTerm && <> correspondant  <strong>"{searchTerm}"</strong></>}
                  {statusFilter !== 'all' && <> ({statusFilter === 'active' ? 'actives' : 'inactives'})</>}
                </span>
              </div>
            )}
          </>
        )}
      </div>

      {showCreateModal && <CreateLanguageModal onClose={() => setShowCreateModal(false)} onCreate={handleCreateLanguage} />}
      {editingLanguage && <EditLanguageModal language={editingLanguage} onClose={() => setEditingLanguage(null)} onEdit={handleEditLanguage} />}
    </>
  );
};

const CreateLanguageModal: React.FC<{
  onClose: () => void;
  onCreate: (data: LanguageCreateData) => void;
}> = ({ onClose, onCreate }) => {
  const [formData, setFormData] = useState<LanguageCreateData>({
    code: '',
    name: '',
    nativeName: '',
    isActive: true,
    displayOrder: 0
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onCreate(formData);
  };

  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.6)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 1000,
      backdropFilter: 'blur(4px)'
    }} onClick={onClose}>
      <div style={{
        backgroundColor: '#ffffff',
        borderRadius: '16px',
        width: '90%',
        maxWidth: '600px',
        maxHeight: '90vh',
        overflow: 'auto',
        boxShadow: '0 20px 60px rgba(0, 0, 0, 0.3)'
      }} onClick={(e) => e.stopPropagation()}>

        {/* Header */}
        <div style={{
          padding: '24px 28px',
          borderBottom: '1px solid #E2E8F0',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          background: 'linear-gradient(135deg, #DC2626 0%, #991B1B 100%)'
        }}>
          <h2 style={{
            margin: 0,
            fontSize: '22px',
            fontWeight: '700',
            color: '#ffffff',
            display: 'flex',
            alignItems: 'center',
            gap: '12px'
          }}>
            <i className="fas fa-language" style={{ fontSize: '24px' }}></i>
            Ajouter une Langue
          </h2>
          <button onClick={onClose} style={{
            background: 'rgba(255, 255, 255, 0.2)',
            border: 'none',
            width: '36px',
            height: '36px',
            borderRadius: '8px',
            cursor: 'pointer',
            color: '#ffffff',
            fontSize: '18px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s'
          }}
          onMouseEnter={(e) => e.currentTarget.style.background = 'rgba(255, 255, 255, 0.3)'}
          onMouseLeave={(e) => e.currentTarget.style.background = 'rgba(255, 255, 255, 0.2)'}
          >
            <i className="fas fa-times"></i>
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} style={{ padding: '28px' }}>
          <div style={{ display: 'flex', flexDirection: 'column', gap: '24px' }}>

            {/* Code de la langue */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Code de la langue <span style={{ color: '#E53E3E' }}>*</span>
              </label>
              <input
                type="text"
                value={formData.code}
                onChange={(e) => setFormData({...formData, code: e.target.value})}
                placeholder="ex: fr, en, es"
                required
                maxLength={10}
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  fontFamily: 'monospace',
                  textTransform: 'uppercase',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
              <small style={{
                display: 'block',
                marginTop: '6px',
                fontSize: '13px',
                color: '#718096'
              }}>
                Code ISO de la langue (2-3 lettres)
              </small>
            </div>

            {/* Nom de la langue */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Nom de la langue <span style={{ color: '#E53E3E' }}>*</span>
              </label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({...formData, name: e.target.value})}
                placeholder="ex: Franais, English"
                required
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
            </div>

            {/* Nom Natif */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Nom Natif
              </label>
              <input
                type="text"
                value={formData.nativeName}
                onChange={(e) => setFormData({...formData, nativeName: e.target.value})}
                placeholder="ex: Franais, English, Espaol"
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
              <small style={{
                display: 'block',
                marginTop: '6px',
                fontSize: '13px',
                color: '#718096'
              }}>
                Nom de la langue dans sa langue native
              </small>
            </div>

            {/* Ordre d'affichage */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Ordre d'affichage
              </label>
              <input
                type="number"
                value={formData.displayOrder}
                onChange={(e) => setFormData({...formData, displayOrder: parseInt(e.target.value) || 0})}
                min="0"
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
              <small style={{
                display: 'block',
                marginTop: '6px',
                fontSize: '13px',
                color: '#718096'
              }}>
                Plus petit = affich en premier
              </small>
            </div>

            {/* Langue active */}
            <div style={{
              padding: '16px',
              backgroundColor: '#F7FAFC',
              borderRadius: '10px',
              border: '2px solid #E2E8F0'
            }}>
              <label style={{
                display: 'flex',
                alignItems: 'center',
                gap: '12px',
                cursor: 'pointer',
                fontSize: '15px',
                fontWeight: '500',
                color: '#2D3748'
              }}>
                <input
                  type="checkbox"
                  checked={formData.isActive}
                  onChange={(e) => setFormData({...formData, isActive: e.target.checked})}
                  style={{
                    width: '20px',
                    height: '20px',
                    cursor: 'pointer',
                    accentColor: '#DC2626'
                  }}
                />
                <span>Langue active (disponible pour slection)</span>
              </label>
            </div>
          </div>

          {/* Footer */}
          <div style={{
            marginTop: '32px',
            display: 'flex',
            gap: '12px',
            justifyContent: 'flex-end'
          }}>
            <button
              type="button"
              onClick={onClose}
              style={{
                padding: '12px 24px',
                fontSize: '15px',
                fontWeight: '600',
                border: '2px solid #E2E8F0',
                borderRadius: '10px',
                backgroundColor: '#ffffff',
                color: '#4A5568',
                cursor: 'pointer',
                transition: 'all 0.2s'
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.backgroundColor = '#F7FAFC';
                e.currentTarget.style.borderColor = '#CBD5E0';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.backgroundColor = '#ffffff';
                e.currentTarget.style.borderColor = '#E2E8F0';
              }}
            >
              Annuler
            </button>
            <button
              type="submit"
              style={{
                padding: '12px 28px',
                fontSize: '15px',
                fontWeight: '600',
                border: 'none',
                borderRadius: '10px',
                background: 'linear-gradient(135deg, #DC2626 0%, #991B1B 100%)',
                color: '#ffffff',
                cursor: 'pointer',
                transition: 'all 0.2s',
                boxShadow: '0 4px 12px rgba(220, 38, 38, 0.4)'
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.transform = 'translateY(-2px)';
                e.currentTarget.style.boxShadow = '0 6px 20px rgba(220, 38, 38, 0.6)';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.transform = 'translateY(0)';
                e.currentTarget.style.boxShadow = '0 4px 12px rgba(220, 38, 38, 0.4)';
              }}
            >
              <i className="fas fa-check" style={{ marginRight: '8px' }}></i>
              Crer la langue
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

const EditLanguageModal: React.FC<{
  language: Language;
  onClose: () => void;
  onEdit: (id: number, updates: LanguageUpdateData) => void;
}> = ({ language, onClose, onEdit }) => {
  const [formData, setFormData] = useState<LanguageUpdateData>({
    code: language.code,
    name: language.name,
    nativeName: language.nativeName || '',
    isActive: language.isActive,
    displayOrder: language.displayOrder
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onEdit(language.id, formData);
  };

  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.6)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 1000,
      backdropFilter: 'blur(4px)'
    }} onClick={onClose}>
      <div style={{
        backgroundColor: '#ffffff',
        borderRadius: '16px',
        width: '90%',
        maxWidth: '600px',
        maxHeight: '90vh',
        overflow: 'auto',
        boxShadow: '0 20px 60px rgba(0, 0, 0, 0.3)'
      }} onClick={(e) => e.stopPropagation()}>

        {/* Header */}
        <div style={{
          padding: '24px 28px',
          borderBottom: '1px solid #E2E8F0',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          background: 'linear-gradient(135deg, #DC2626 0%, #991B1B 100%)'
        }}>
          <h2 style={{
            margin: 0,
            fontSize: '22px',
            fontWeight: '700',
            color: '#ffffff',
            display: 'flex',
            alignItems: 'center',
            gap: '12px'
          }}>
            <i className="fas fa-edit" style={{ fontSize: '24px' }}></i>
            Modifier la Langue
          </h2>
          <button onClick={onClose} style={{
            background: 'rgba(255, 255, 255, 0.2)',
            border: 'none',
            width: '36px',
            height: '36px',
            borderRadius: '8px',
            cursor: 'pointer',
            color: '#ffffff',
            fontSize: '18px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s'
          }}
          onMouseEnter={(e) => e.currentTarget.style.background = 'rgba(255, 255, 255, 0.3)'}
          onMouseLeave={(e) => e.currentTarget.style.background = 'rgba(255, 255, 255, 0.2)'}
          >
            <i className="fas fa-times"></i>
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} style={{ padding: '28px' }}>
          <div style={{ display: 'flex', flexDirection: 'column', gap: '24px' }}>

            {/* Code de la langue */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Code de la langue <span style={{ color: '#E53E3E' }}>*</span>
              </label>
              <input
                type="text"
                value={formData.code}
                onChange={(e) => setFormData({...formData, code: e.target.value})}
                placeholder="ex: fr, en, es"
                required
                maxLength={10}
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  fontFamily: 'monospace',
                  textTransform: 'uppercase',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
              <small style={{
                display: 'block',
                marginTop: '6px',
                fontSize: '13px',
                color: '#718096'
              }}>
                Code ISO de la langue (2-3 lettres)
              </small>
            </div>

            {/* Nom de la langue */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Nom de la langue <span style={{ color: '#E53E3E' }}>*</span>
              </label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({...formData, name: e.target.value})}
                placeholder="ex: Franais, English"
                required
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
            </div>

            {/* Nom Natif */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Nom Natif
              </label>
              <input
                type="text"
                value={formData.nativeName}
                onChange={(e) => setFormData({...formData, nativeName: e.target.value})}
                placeholder="ex: Franais, English, Espaol"
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
              <small style={{
                display: 'block',
                marginTop: '6px',
                fontSize: '13px',
                color: '#718096'
              }}>
                Nom de la langue dans sa langue native
              </small>
            </div>

            {/* Ordre d'affichage */}
            <div>
              <label style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#2D3748'
              }}>
                Ordre d'affichage
              </label>
              <input
                type="number"
                value={formData.displayOrder}
                onChange={(e) => setFormData({...formData, displayOrder: parseInt(e.target.value) || 0})}
                min="0"
                style={{
                  width: '100%',
                  padding: '12px 16px',
                  fontSize: '15px',
                  border: '2px solid #E2E8F0',
                  borderRadius: '10px',
                  outline: 'none',
                  transition: 'all 0.2s',
                  boxSizing: 'border-box'
                }}
                onFocus={(e) => e.target.style.borderColor = '#DC2626'}
                onBlur={(e) => e.target.style.borderColor = '#E2E8F0'}
              />
              <small style={{
                display: 'block',
                marginTop: '6px',
                fontSize: '13px',
                color: '#718096'
              }}>
                Plus petit = affich en premier
              </small>
            </div>

            {/* Langue active */}
            <div style={{
              padding: '16px',
              backgroundColor: '#F7FAFC',
              borderRadius: '10px',
              border: '2px solid #E2E8F0'
            }}>
              <label style={{
                display: 'flex',
                alignItems: 'center',
                gap: '12px',
                cursor: 'pointer',
                fontSize: '15px',
                fontWeight: '500',
                color: '#2D3748'
              }}>
                <input
                  type="checkbox"
                  checked={formData.isActive}
                  onChange={(e) => setFormData({...formData, isActive: e.target.checked})}
                  style={{
                    width: '20px',
                    height: '20px',
                    cursor: 'pointer',
                    accentColor: '#DC2626'
                  }}
                />
                <span>Langue active (disponible pour slection)</span>
              </label>
              {language.participantCount && language.participantCount > 0 && (
                <div style={{
                  marginTop: '12px',
                  padding: '12px',
                  backgroundColor: '#FFF5E6',
                  borderRadius: '8px',
                  border: '1px solid #FFD580',
                  display: 'flex',
                  alignItems: 'center',
                  gap: '8px'
                }}>
                  <i className="fas fa-exclamation-triangle" style={{ color: '#D97706' }}></i>
                  <small style={{ color: '#92400E', fontSize: '13px' }}>
                    Cette langue est utilise par <strong>{language.participantCount}</strong> participant(s)
                  </small>
                </div>
              )}
            </div>
          </div>

          {/* Footer */}
          <div style={{
            marginTop: '32px',
            display: 'flex',
            gap: '12px',
            justifyContent: 'flex-end'
          }}>
            <button
              type="button"
              onClick={onClose}
              style={{
                padding: '12px 24px',
                fontSize: '15px',
                fontWeight: '600',
                border: '2px solid #E2E8F0',
                borderRadius: '10px',
                backgroundColor: '#ffffff',
                color: '#4A5568',
                cursor: 'pointer',
                transition: 'all 0.2s'
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.backgroundColor = '#F7FAFC';
                e.currentTarget.style.borderColor = '#CBD5E0';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.backgroundColor = '#ffffff';
                e.currentTarget.style.borderColor = '#E2E8F0';
              }}
            >
              Annuler
            </button>
            <button
              type="submit"
              style={{
                padding: '12px 28px',
                fontSize: '15px',
                fontWeight: '600',
                border: 'none',
                borderRadius: '10px',
                background: 'linear-gradient(135deg, #DC2626 0%, #991B1B 100%)',
                color: '#ffffff',
                cursor: 'pointer',
                transition: 'all 0.2s',
                boxShadow: '0 4px 12px rgba(220, 38, 38, 0.4)'
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.transform = 'translateY(-2px)';
                e.currentTarget.style.boxShadow = '0 6px 20px rgba(220, 38, 38, 0.6)';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.transform = 'translateY(0)';
                e.currentTarget.style.boxShadow = '0 4px 12px rgba(220, 38, 38, 0.4)';
              }}
            >
              <i className="fas fa-check" style={{ marginRight: '8px' }}></i>
              Enregistrer
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Languages;
